<template>
  <div class="weather-container">
    <h2 class="title">Погода</h2>
    <hr />
    <div class="city-selection">
      <label for="city">Выберите город:</label>
      <select id="city" v-model="selectedCity" @change="fetchWeatherData">
        <option v-for="city in cities" :key="city.id" :value="city.name">
          {{ city.name }}
        </option>
      </select>
      <button type="button" class="btn-fetch" @click="fetchWeatherData">
        Узнать погоду
      </button>
    </div>
    <hr />
    <div class="content" v-if="isWeatherDataAvailable">
      <CurrentWeather :city="selectedCity" :weather="fetchedWeather" />
      <WeatherForecastDisplay :forecastData="forecastData" />
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import CurrentWeather from './components/CurrentWeather.vue';
import WeatherForecastDisplay from './components/WeatherForecastDisplay.vue';

export default {
  components: {
    CurrentWeather,
    WeatherForecastDisplay,
  },
  data() {
    return {
      selectedCity: 'Москва',
      cities: [
        { id: 1, name: 'Москва' },
        { id: 2, name: 'Санкт-Петербург' },
        { id: 3, name: 'Новосибирск' },
        { id: 4, name: 'Екатеринбург' },
        { id: 5, name: 'Казань' },
      ],
      fetchedWeather: null,
      forecastData: null,
    };
  },
  computed: {
    isWeatherDataAvailable() {
      return this.fetchedWeather && this.forecastData;
    },
  },
  methods: {
    async fetchWeatherData() {
      try {
        const weatherResponse = await axios.get(
          `https://api.openweathermap.org/data/2.5/weather?q=${this.selectedCity}&appid=78dd6c28feb0c9c8c84b5a3adf3d9ebd&units=metric`
        );
        this.fetchedWeather = weatherResponse.data;

        const forecastResponse = await axios.get(
          `https://api.openweathermap.org/data/2.5/forecast?q=${this.selectedCity}&appid=78dd6c28feb0c9c8c84b5a3adf3d9ebd&units=metric`
        );
        this.forecastData = forecastResponse.data.list;
      } catch (error) {
        console.error('Ошибка при получении данных о погоде:', error);
      }
    },
  },
};
</script>


