<template>
    <div class="current-weather">
      <h3>Текущая погода для {{ city }}</h3>
      <table class="weather-table">
        <tbody>
          <tr>
            <td>Температура: {{ weather.main.temp }} °C</td>
            <td>Влажность: {{ weather.main.humidity }}%</td>
            <td>Давление: {{ weather.main.pressure }} ГПа</td>
            <td>Погода: {{ weather.weather[0].description }}</td>
            <td>
              <img :src="weatherIconUrl" alt="Weather Icon" class="weather-icon" />
            </td>
            <td>Скорость ветра: {{ weather.wind.speed }} М/с</td>
          </tr>
        </tbody>
      </table>
    </div>
  </template>
  
  <script>
  export default {
    props: {
      city: {
        type: String,
        required: true,
      },
      weather: {
        type: Object,
        required: true,
      },
    },
    computed: {
      weatherIconUrl() {
        return `http://openweathermap.org/img/wn/${this.weather.weather[0].icon}@2x.png`;
      },
    },
  };
  </script>
  
  <style scoped>
  .current-weather {
    margin: 20px 0;
  }
  
  .weather-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  .weather-table td {
    padding: 10px;
    border: 1px solid #b2dfdb;
    background-color: #ffffff;
    text-align: center;
  }
  .weather-icon {
    width: 50px;
  }
  </style>