<template>
    <div class="weather-forecast-display">
      <h4>Прогноз погоды на ближайшие дни</h4>
      <div class="forecast-items">
        <div class="forecast-item" v-for="(forecast, index) in dailyForecast" :key="index">
          <p>{{ formatDate(forecast.dt) }}</p>
          <img :src="getWeatherIcon(forecast.weather[0].icon)" alt="Weather Icon" />
          <p>Температура: {{ forecast.main.temp }} °C</p>
          <p>Описание: {{ forecast.weather[0].description }}</p>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    props: {
      forecastData: {
        type: Array,
        required: true,
      },
    },
    computed: {
      dailyForecast() {
        return this.forecastData.filter((_, index) => index % 8 === 0); // Получаем данные каждые 8 часов
      },
    },
    methods: {
      formatDate(timestamp) {
        const date = new Date(timestamp * 1000);
        return date.toLocaleDateString('ru-RU', { weekday: 'long', year: 'numeric', month: 'numeric', day: 'numeric' });
      },
      getWeatherIcon(icon) {
        return `http://openweathermap.org/img/wn/${icon}@2x.png`;
      },
    },
  };
  </script>
  
  <style scoped>
  .weather-forecast-display {
    margin-top: 20px;
    text-align: center;
  }
  
  .forecast-items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .forecast-item {
    margin: 10px;
    padding: 10px;
    border: 1px solid #b2dfdb;
    border-radius: 10px;
    background-color: #ffffff;
    width: 150px; /* Ширина блока прогноза */
  }
  
  .forecast-item img {
    width: 60px; /* Размер иконки */
  }
  </style>